*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body{
  width: 100%;
  height: 100%;
}


@media screen and (max-width: 767px){

  header, footer{
    display: none;
  }

  nav{
    text-align: center;     
    height: 20%;
    overflow: auto;
    list-style-type: none; 
    }

  #menu {
    width: 100%;
    text-align:center;
    color:#fff;
    font-weight: bolder;
  }

  #menu > li {
    margin: 2% 0 0 0;
    background-color: #0000ff; 
  }

  #menu > li > ul{
    color: #fff;
    width: 66%;
    height: 200px;
    overflow: auto;
    position: absolute;
    padding: 2% 0;
    left: 17%;
    top: 20%;
    display: none;
  }

  #menu > li > ul > li{
    padding: 2% 0;
    background-color: #0000ff; 
    margin: 2% 0 0 0;
  }

  #menu > li:hover > ul{
    display: block;
  }
  
}      

//stylowanie mapki

#map{
  width: 100%;
  height: 200px;
}

img{
  width: 100%;
  height: auto;
}


